"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";

interface InvoiceItem {
  id: number;
  name: string;
  sku: string;
  quantity: number;
  price: number;
  tax: number;
  total: number;
}

interface InvoiceData {
  invoiceNumber: string;
  date: string;
  dueDate: string;
  platform: string;
  customerName: string;
  customerEmail: string;
  customerPhone: string;
  customerAddress: string;
  items: InvoiceItem[];
  subtotal: number;
  taxAmount: number;
  discount: number;
  total: number;
  notes: string;
}

const mockInvoiceData: InvoiceData = {
  invoiceNumber: "INV-2024-001",
  date: new Date().toISOString().split('T')[0],
  dueDate: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
  platform: "direct",
  customerName: "John Doe",
  customerEmail: "john.doe@email.com",
  customerPhone: "+91 9876543210",
  customerAddress: "123 Main Street, Mumbai, Maharashtra 400001",
  items: [
    {
      id: 1,
      name: "iPhone 15 Pro Max",
      sku: "SKU001",
      quantity: 1,
      price: 134900,
      tax: 18,
      total: 159202
    },
    {
      id: 2,
      name: "AirPods Pro 2",
      sku: "SKU005",
      quantity: 2,
      price: 24900,
      tax: 18,
      total: 58764
    }
  ],
  subtotal: 184700,
  taxAmount: 33266,
  discount: 0,
  total: 217966,
  notes: "Thank you for your business!"
};

export default function InvoicePage() {
  const [invoiceData, setInvoiceData] = useState<InvoiceData>(mockInvoiceData);
  const [selectedPlatform, setSelectedPlatform] = useState("direct");
  const [showPreview, setShowPreview] = useState(false);

  const addItem = () => {
    const newItem: InvoiceItem = {
      id: Date.now(),
      name: "",
      sku: "",
      quantity: 1,
      price: 0,
      tax: 18,
      total: 0
    };
    setInvoiceData({
      ...invoiceData,
      items: [...invoiceData.items, newItem]
    });
  };

  const updateItem = (id: number, field: keyof InvoiceItem, value: any) => {
    const updatedItems = invoiceData.items.map(item => {
      if (item.id === id) {
        const updatedItem = { ...item, [field]: value };
        if (field === 'quantity' || field === 'price' || field === 'tax') {
          updatedItem.total = updatedItem.quantity * updatedItem.price * (1 + updatedItem.tax / 100);
        }
        return updatedItem;
      }
      return item;
    });

    const subtotal = updatedItems.reduce((sum, item) => sum + (item.quantity * item.price), 0);
    const taxAmount = updatedItems.reduce((sum, item) => sum + (item.quantity * item.price * item.tax / 100), 0);
    const total = subtotal + taxAmount - invoiceData.discount;

    setInvoiceData({
      ...invoiceData,
      items: updatedItems,
      subtotal,
      taxAmount,
      total
    });
  };

  const removeItem = (id: number) => {
    const updatedItems = invoiceData.items.filter(item => item.id !== id);
    const subtotal = updatedItems.reduce((sum, item) => sum + (item.quantity * item.price), 0);
    const taxAmount = updatedItems.reduce((sum, item) => sum + (item.quantity * item.price * item.tax / 100), 0);
    const total = subtotal + taxAmount - invoiceData.discount;

    setInvoiceData({
      ...invoiceData,
      items: updatedItems,
      subtotal,
      taxAmount,
      total
    });
  };

  const generatePDF = () => {
    alert("PDF generation initiated. Invoice will be downloaded shortly.");
  };

  const sendEmail = () => {
    alert(`Invoice sent to ${invoiceData.customerEmail}`);
  };

  const generateQRCode = () => {
    alert("QR Code generated for payment");
  };

  const generateBarcode = () => {
    alert("Barcode generated for invoice tracking");
  };

  const PlatformInvoiceTemplate = ({ platform }: { platform: string }) => {
    const platformStyles = {
      amazon: "border-orange-200 bg-orange-50",
      flipkart: "border-blue-200 bg-blue-50",
      meesho: "border-purple-200 bg-purple-50",
      direct: "border-gray-200 bg-gray-50"
    };

    const platformColors = {
      amazon: "text-orange-800",
      flipkart: "text-blue-800",
      meesho: "text-purple-800",
      direct: "text-gray-800"
    };

    return (
      <div className={`p-6 rounded-lg border-2 ${platformStyles[platform as keyof typeof platformStyles]}`}>
        <div className="flex justify-between items-start mb-6">
          <div>
            <h2 className={`text-2xl font-bold ${platformColors[platform as keyof typeof platformColors]}`}>
              {platform === 'amazon' ? 'AMAZON INVOICE' :
               platform === 'flipkart' ? 'FLIPKART INVOICE' :
               platform === 'meesho' ? 'MEESHO INVOICE' :
               'INVOICE'}
            </h2>
            <p className="text-sm text-gray-600">Invoice #{invoiceData.invoiceNumber}</p>
          </div>
          <div className="text-right">
            <p className="text-sm text-gray-600">Date: {invoiceData.date}</p>
            <p className="text-sm text-gray-600">Due: {invoiceData.dueDate}</p>
          </div>
        </div>

        <div className="grid grid-cols-2 gap-6 mb-6">
          <div>
            <h3 className="font-semibold mb-2">Bill To:</h3>
            <p className="text-sm">{invoiceData.customerName}</p>
            <p className="text-sm">{invoiceData.customerEmail}</p>
            <p className="text-sm">{invoiceData.customerPhone}</p>
            <p className="text-sm">{invoiceData.customerAddress}</p>
          </div>
          <div>
            <h3 className="font-semibold mb-2">Platform Details:</h3>
            <Badge variant="outline" className="mb-2">
              {platform.toUpperCase()}
            </Badge>
            <div className="space-y-1">
              <div className="w-16 h-16 bg-gray-200 rounded flex items-center justify-center text-xs">
                QR Code
              </div>
              <div className="w-24 h-6 bg-gray-200 rounded flex items-center justify-center text-xs">
                Barcode
              </div>
            </div>
          </div>
        </div>

        <div className="mb-6">
          <table className="w-full border-collapse border border-gray-300">
            <thead>
              <tr className="bg-gray-100">
                <th className="border border-gray-300 p-2 text-left">Item</th>
                <th className="border border-gray-300 p-2 text-left">SKU</th>
                <th className="border border-gray-300 p-2 text-right">Qty</th>
                <th className="border border-gray-300 p-2 text-right">Price</th>
                <th className="border border-gray-300 p-2 text-right">Tax</th>
                <th className="border border-gray-300 p-2 text-right">Total</th>
              </tr>
            </thead>
            <tbody>
              {invoiceData.items.map(item => (
                <tr key={item.id}>
                  <td className="border border-gray-300 p-2">{item.name}</td>
                  <td className="border border-gray-300 p-2">{item.sku}</td>
                  <td className="border border-gray-300 p-2 text-right">{item.quantity}</td>
                  <td className="border border-gray-300 p-2 text-right">₹{item.price.toLocaleString()}</td>
                  <td className="border border-gray-300 p-2 text-right">{item.tax}%</td>
                  <td className="border border-gray-300 p-2 text-right">₹{item.total.toLocaleString()}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        <div className="flex justify-end">
          <div className="w-64">
            <div className="flex justify-between py-1">
              <span>Subtotal:</span>
              <span>₹{invoiceData.subtotal.toLocaleString()}</span>
            </div>
            <div className="flex justify-between py-1">
              <span>Tax (GST):</span>
              <span>₹{invoiceData.taxAmount.toLocaleString()}</span>
            </div>
            <div className="flex justify-between py-1">
              <span>Discount:</span>
              <span>₹{invoiceData.discount.toLocaleString()}</span>
            </div>
            <Separator className="my-2" />
            <div className="flex justify-between py-2 font-bold text-lg">
              <span>Total:</span>
              <span>₹{invoiceData.total.toLocaleString()}</span>
            </div>
          </div>
        </div>

        {invoiceData.notes && (
          <div className="mt-6">
            <h3 className="font-semibold mb-2">Notes:</h3>
            <p className="text-sm">{invoiceData.notes}</p>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Invoice Generator</h1>
          <p className="text-gray-600">Create professional invoices with QR codes, barcodes, and GST calculations</p>
        </div>
        
        <div className="flex gap-2">
          <Button onClick={generateQRCode} variant="outline" size="sm">
            Generate QR Code
          </Button>
          <Button onClick={generateBarcode} variant="outline" size="sm">
            Generate Barcode
          </Button>
          <Button onClick={generatePDF} size="sm">
            Download PDF
          </Button>
        </div>
      </div>

      <Tabs defaultValue="create" className="space-y-6">
        <TabsList>
          <TabsTrigger value="create">Create Invoice</TabsTrigger>
          <TabsTrigger value="preview">Preview</TabsTrigger>
          <TabsTrigger value="templates">Platform Templates</TabsTrigger>
        </TabsList>

        <TabsContent value="create" className="space-y-6">
          {/* Invoice Form */}
          <Card>
            <CardHeader>
              <CardTitle>Invoice Details</CardTitle>
              <CardDescription>Fill in the invoice information</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Basic Info */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <Label htmlFor="invoiceNumber">Invoice Number</Label>
                  <Input
                    id="invoiceNumber"
                    value={invoiceData.invoiceNumber}
                    onChange={(e) => setInvoiceData({...invoiceData, invoiceNumber: e.target.value})}
                  />
                </div>
                <div>
                  <Label htmlFor="date">Date</Label>
                  <Input
                    id="date"
                    type="date"
                    value={invoiceData.date}
                    onChange={(e) => setInvoiceData({...invoiceData, date: e.target.value})}
                  />
                </div>
                <div>
                  <Label htmlFor="platform">Platform</Label>
                  <Select value={selectedPlatform} onValueChange={setSelectedPlatform}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="direct">Direct Sale</SelectItem>
                      <SelectItem value="amazon">Amazon</SelectItem>
                      <SelectItem value="flipkart">Flipkart</SelectItem>
                      <SelectItem value="meesho">Meesho</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              {/* Customer Info */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold">Customer Information</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="customerName">Customer Name</Label>
                    <Input
                      id="customerName"
                      value={invoiceData.customerName}
                      onChange={(e) => setInvoiceData({...invoiceData, customerName: e.target.value})}
                    />
                  </div>
                  <div>
                    <Label htmlFor="customerEmail">Email</Label>
                    <Input
                      id="customerEmail"
                      type="email"
                      value={invoiceData.customerEmail}
                      onChange={(e) => setInvoiceData({...invoiceData, customerEmail: e.target.value})}
                    />
                  </div>
                  <div>
                    <Label htmlFor="customerPhone">Phone</Label>
                    <Input
                      id="customerPhone"
                      value={invoiceData.customerPhone}
                      onChange={(e) => setInvoiceData({...invoiceData, customerPhone: e.target.value})}
                    />
                  </div>
                  <div>
                    <Label htmlFor="customerAddress">Address</Label>
                    <Textarea
                      id="customerAddress"
                      value={invoiceData.customerAddress}
                      onChange={(e) => setInvoiceData({...invoiceData, customerAddress: e.target.value})}
                    />
                  </div>
                </div>
              </div>

              {/* Items */}
              <div className="space-y-4">
                <div className="flex justify-between items-center">
                  <h3 className="text-lg font-semibold">Invoice Items</h3>
                  <Button onClick={addItem} size="sm">Add Item</Button>
                </div>
                
                <div className="space-y-3">
                  {invoiceData.items.map(item => (
                    <div key={item.id} className="grid grid-cols-12 gap-2 items-end p-3 border rounded">
                      <div className="col-span-3">
                        <Label>Product Name</Label>
                        <Input
                          value={item.name}
                          onChange={(e) => updateItem(item.id, 'name', e.target.value)}
                          placeholder="Product name"
                        />
                      </div>
                      <div className="col-span-2">
                        <Label>SKU</Label>
                        <Input
                          value={item.sku}
                          onChange={(e) => updateItem(item.id, 'sku', e.target.value)}
                          placeholder="SKU"
                        />
                      </div>
                      <div className="col-span-1">
                        <Label>Qty</Label>
                        <Input
                          type="number"
                          value={item.quantity}
                          onChange={(e) => updateItem(item.id, 'quantity', parseInt(e.target.value))}
                        />
                      </div>
                      <div className="col-span-2">
                        <Label>Price</Label>
                        <Input
                          type="number"
                          value={item.price}
                          onChange={(e) => updateItem(item.id, 'price', parseFloat(e.target.value))}
                        />
                      </div>
                      <div className="col-span-1">
                        <Label>Tax %</Label>
                        <Input
                          type="number"
                          value={item.tax}
                          onChange={(e) => updateItem(item.id, 'tax', parseFloat(e.target.value))}
                        />
                      </div>
                      <div className="col-span-2">
                        <Label>Total</Label>
                        <Input value={`₹${item.total.toLocaleString()}`} disabled />
                      </div>
                      <div className="col-span-1">
                        <Button
                          onClick={() => removeItem(item.id)}
                          variant="destructive"
                          size="sm"
                        >
                          Remove
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* Totals */}
              <div className="flex justify-end">
                <div className="w-64 space-y-2">
                  <div className="flex justify-between">
                    <span>Subtotal:</span>
                    <span>₹{invoiceData.subtotal.toLocaleString()}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Tax (GST):</span>
                    <span>₹{invoiceData.taxAmount.toLocaleString()}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Discount:</span>
                    <Input
                      type="number"
                      value={invoiceData.discount}
                      onChange={(e) => setInvoiceData({...invoiceData, discount: parseFloat(e.target.value) || 0})}
                      className="w-24 h-6 text-right"
                    />
                  </div>
                  <Separator />
                  <div className="flex justify-between font-bold text-lg">
                    <span>Total:</span>
                    <span>₹{invoiceData.total.toLocaleString()}</span>
                  </div>
                </div>
              </div>

              {/* Notes */}
              <div>
                <Label htmlFor="notes">Notes</Label>
                <Textarea
                  id="notes"
                  value={invoiceData.notes}
                  onChange={(e) => setInvoiceData({...invoiceData, notes: e.target.value})}
                  placeholder="Additional notes or terms"
                />
              </div>

              {/* Actions */}
              <div className="flex gap-2">
                <Button onClick={() => setShowPreview(true)}>Preview Invoice</Button>
                <Button onClick={generatePDF} variant="outline">Generate PDF</Button>
                <Button onClick={sendEmail} variant="outline">Send Email</Button>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="preview">
          <Card>
            <CardContent className="p-6">
              <PlatformInvoiceTemplate platform={selectedPlatform} />
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="templates">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            <Card>
              <CardHeader>
                <CardTitle>Amazon Invoice Template</CardTitle>
                <CardDescription>Optimized for Amazon marketplace</CardDescription>
              </CardHeader>
              <CardContent>
                <PlatformInvoiceTemplate platform="amazon" />
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Flipkart Invoice Template</CardTitle>
                <CardDescription>Optimized for Flipkart marketplace</CardDescription>
              </CardHeader>
              <CardContent>
                <PlatformInvoiceTemplate platform="flipkart" />
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Meesho Invoice Template</CardTitle>
                <CardDescription>Optimized for Meesho marketplace</CardDescription>
              </CardHeader>
              <CardContent>
                <PlatformInvoiceTemplate platform="meesho" />
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Direct Sale Template</CardTitle>
                <CardDescription>For direct customer sales</CardDescription>
              </CardHeader>
              <CardContent>
                <PlatformInvoiceTemplate platform="direct" />
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}
